import type { TimeDataItem } from './Timeline';
import type { EventDefinition } from './core/EventHandler';

/** CSS 属性键值对 */
type Style = CSSStyleDeclaration &
  Record<':hover' | ':active' | '::before' | '::after' | ':disabled', CSSStyleDeclaration>;

/** 主题 */
export const THEME = {
  /** 移动端 */
  MOBILE: 'mobile',
  /** 移动端-夜间版 */
  MOBILE_DARK: 'mobile-dark',
  /** 移动端-外部显示刻度值（刻度值上下布局） */
  MOBILE_WITH_VALUE_OUTER: 'mobile-withValueOuter',
  /** 移动端-外部显示刻度值（刻度值上下布局）-夜间版 */
  MOBILE_WITH_VALUE_OUTER_DARK: 'mobile-withValueOuter-dark',
  /** 移动端-窄版 */
  MOBILE_COMPACT: 'mobile-compact',
  /** 移动端-窄版-夜间版 */
  MOBILE_COMPACT_DARK: 'mobile-compact-dark',
  /** 移动端-窄版-外部显示刻度值（刻度值上下布局） */
  MOBILE_COMPACT_WITH_VALUE_OUTER: 'mobile-compact-withValueOuter',
  /** 移动端-窄版-外部显示刻度值（刻度值上下布局）-夜间版 */
  MOBILE_COMPACT_WITH_VALUE_OUTER_DARK: 'mobile-compact-withValueOuter-dark',
  /** 移动端-简单版（无操作按钮） */
  MOBILE_SIMPLE: 'mobile-simple',
  /** 移动端-简单版（无操作按钮）-夜间版 */
  MOBILE_SIMPLE_DARK: 'mobile-simple-dark',
  /** PC 端 */
  PC: 'pc',
  /** PC 端-夜间版 */
  PC_DARK: 'pc-dark'
} as const;

export const SPEED_TEXT_MAP = {
  '0.5': '0.5',
  '0.75': '0.75',
  '1': '1.0',
  '1.25': '1.25',
  '1.5': '1.5',
  '2': '2.0'
} as const;

/**
 * 时间轴配置项
 */
export type Option = {
  /** 时间轴数据 */
  data: TimeDataItem[];
  /** 主题配置，默认为 'mobile' */
  theme?: typeof THEME[keyof typeof THEME];
  /** 配置项 */
  config: {
    /** 时间轴 */
    axis?: {
      /** 轴线 */
      background?: {
        /** 样式（自定义） */
        style?: Style;
      };
      /** 区间进度条（配置了 dataIndexRange 后自动显示） */
      progressRange?: {
        style?: Style;
      };
      /** 累积进度条 */
      progress?: {
        /** 显示 */
        show: boolean;
        style?: Style;
      };
      /** 刻度线 */
      tickLine?: {
        /** 主刻度 */
        main?: {
          style?: Style;
        };
        /** 次要刻度 */
        sub?: {
          style?: Style;
        };
      };
      /** 刻度值 */
      tickValue?: {
        style?: Style;
        /** 刻度值（自定义） */
        formatter?: (timeData: TimeDataItem, index: number) => string;
      };
      /** 按钮 */
      marker?: {
        style?: Style;
      };
      /** 提示框 */
      tooltip?: {
        style?: Style;
        /** 箭头样式 */
        arrowStyle?: Style;
        /** 文本样式 */
        textStyle?: Style;
        /** 提示（自定义） */
        formatter?: (timeData: TimeDataItem, index: number) => string;
      };
      /** 交互 */
      action?: {
        /** 禁止轴上的点击交互 */
        clickDisabled?: boolean;
        /** 开启拖动滑块实时触发 change 事件 */
        enableDragChange?: boolean;
        /** 索引区间交互（配置了 dataIndexRange 后有效） */
        dataIndexRange?: {
          /** 禁止区间起始位置的拖动按钮 */
          disableStartBtn?: boolean;
          /** 禁止结束位置的拖动按钮改变索引区间 */
          disableEndBtnChangeRange?: boolean;
        };
      };
    };
    /** 操作栏 */
    operation?: {
      /** 播放按钮 */
      playButton?: {
        style?: Style;
      };
      /** 暂停按钮 */
      pauseButton?: {
        style?: Style;
      };
      /** 上一期按钮 */
      previousButton?: {
        style?: Style;
        text?: string;
      };
      /** 下一期按钮 */
      nextButton?: {
        style?: Style;
        text?: string;
      };
      /** 倍速设置按钮 */
      speed: {
        /** 是否开启 */
        enable: boolean;
        /** 按钮样式 */
        style?: Style;
        /** 按钮文本样式 */
        textStyle?: Style;
        /** 按钮右上角标签样式 */
        labelStyle?: Style;
        /** 弹窗菜单背景遮罩样式 */
        menuListBackgroundMaskStyle?: Style;
        /** 弹窗菜单列表样式 */
        menuListStyle?: Style;
        /** 弹窗菜单列表项样式 */
        menuListItemStyle?: Style;
        /** 弹窗菜单列表项文本样式 */
        menuListItemTextStyle?: Style;
      };
    };
    /** 起始位置的数据索引 */
    dataIndex?: number;
    /** 数据索引区间限制 开闭 [,) */
    dataIndexRange?: [number, number];
    /** 动画配置项 */
    animation: {
      /** 动画播放单个周期持续时长 */
      intervalTime: number;
      /** 默认播放速度（1.0） */
      speed: keyof typeof SPEED_TEXT_MAP;
      /** 在什么交互情况下要触发动画（默认所有交互情况都触发动画，连续两次交互会等待前一周期动画播放完毕再触发 change 事件），不触发动画的交互可以立即触发 change 事件，不用等待前一周期动画播放完毕 */
      causeBy?: Parameters<EventDefinition['change']>[0]['causeBy'][];
    };
  };
};
