export type Point = {
  type: 'main' | 'sub';
  index: number;
  hide: boolean;
  position: number;
};

/** 次要刻度最小间距 */
const MIN_SUB_SPACE = 4;

/**
 * 点比例尺
 */
export default class PointScale {
  /** 值域 */
  private _range: [number, number] = [0, 0];

  /** 定义域 */
  private _domain: Point[] = [];

  /** 两侧外边距 */
  private _padding: [number, number] = [0, 0];

  constructor(range?: [number, number], domain?: Pick<Point, 'type' | 'index'>[]) {
    if (typeof range !== 'undefined') {
      this.range(range);
    }
    if (typeof domain !== 'undefined') {
      // deepClone
      this.domain(domain);
    }
  }

  /**
   * 计算解析
   */
  private __parse() {
    if (this._range[0] === this._range[1] || this._domain.length === 0) {
      return;
    }

    const mainIndexes = this._domain.reduce((indexes: number[], point, index) => {
      if (point.type === 'main') {
        return [...indexes, index];
      }
      return indexes;
    }, []);
    const mainNum = mainIndexes.length;
    const subRange = [
      0,
      (this._range[1] - this._padding[1] - this._range[0] - this._padding[0]) / (mainNum - 1)
    ];

    let currentMainNum = -1;
    let currentSubNum = 0;
    let skipSubNum = 0;

    const domain: Point[] = [];
    this._domain.slice().forEach(point_ => {
      const point = { ...point_ };

      if (point.type === 'main') {
        currentMainNum += 1;
        currentSubNum = 0;
        skipSubNum = 0;

        const space = subRange[1] - subRange[0];

        point.position = this._padding[0] + this._range[0] + space * currentMainNum;
      } else {
        currentSubNum += 1;

        const currentMainIndex = mainIndexes[currentMainNum];
        const nextMainIndex = mainIndexes[currentMainNum + 1];
        const space = (subRange[1] - subRange[0]) / (nextMainIndex - currentMainIndex);

        while (space * skipSubNum < MIN_SUB_SPACE) {
          skipSubNum += 1;
        }

        // ! 不满足最小间距的隐藏
        if (currentSubNum % skipSubNum !== 0) {
          point.hide = true;
        } else {
          point.hide = false;
        }

        point.position = domain[currentMainIndex].position + space * currentSubNum;
      }

      domain.push(point);
    });

    this._domain = domain;
  }

  /**
   * 值域
   */
  range(range_?: [number, number]): [number, number] {
    if (typeof range_ !== 'undefined' && Array.isArray(range_) && range_.length > 0) {
      if (
        Number.isNaN(Number(range_[0])) ||
        Number.isNaN(Number(range_[1])) ||
        range_[0] >= range_[1]
      ) {
        throw new Error(`"range" should be: range[0] < range[1], ${JSON.stringify(range_)}`);
      }

      this._range = [...range_];
      this.__parse();
    }

    return [...this._range];
  }

  /**
   * 定义域
   */
  domain(domain_?: Pick<Point, 'type' | 'index'>[]) {
    if (typeof domain_ !== 'undefined' && Array.isArray(domain_) && domain_.length > 0) {
      // deepClone
      this._domain = domain_.map(item => ({
        ...item,
        hide: false,
        position: 0
      }));
      this.__parse();
    }

    // deepClone
    return [...this._domain];
  }

  /**
   * 两侧外边距
   */
  padding(padding_?: [number, number]): [number, number] {
    if (typeof padding_ !== 'undefined' && Array.isArray(padding_) && padding_.length > 0) {
      if (
        Number.isNaN(Number(padding_[0])) ||
        Number.isNaN(Number(padding_[1])) ||
        padding_[0] < 0 ||
        padding_[1] < 0
      ) {
        throw new Error(
          `"padding" should be: range[0] >= 0 and range[1] >= 0, ${JSON.stringify(padding_)}`
        );
      }

      this._padding = [...padding_];
      this.__parse();
    }

    return [...this._padding];
  }

  /**
   * 获取点所在的坐标位置
   */
  at(index: Point['index']) {
    if (!this._domain[index]) {
      throw new Error(
        `"index" ${index} should be one of: ${this._domain.map(p => p.index).join(' | ')}`
      );
    }

    return this._domain[index].position;
  }

  /**
   * 根据坐标位置获取当前点
   */
  findPointAt(position: Point['position']) {
    const range = this._range.slice();

    if (!range.length || position < range[0] || position > range[1]) {
      throw new Error(`"position" ${position} should be within "range" ${JSON.stringify(range)}`);
    }

    const domain = this._domain.slice();
    let index = 0;
    const MIDDLE = 0.5;

    for (let i = 0; i < domain.length; i += 1) {
      const currentPosition = domain[i].position;
      const nextPosition = domain[i + 1]?.position;

      if (currentPosition <= position && (!nextPosition || nextPosition > position)) {
        if (position <= currentPosition * (1 - MIDDLE) + nextPosition * MIDDLE) {
          index = i;
        } else {
          index = i + 1;
        }

        break;
      }
    }

    index = Math.max(0, index);
    index = Math.min(index, domain.length - 1);

    // deepClone
    return { ...this._domain[index] };
  }
}
